@import url(https://fonts.googleapis.com/css?family=Lato:400,700|Merriweather:700);
html,
body {
  margin: 0 0;
  padding: 0 0;
  width: 100%;
  height: 100%;
  font-family: "Lato", "Helvetica";
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

* {
  box-sizing: border-box;
}

#root {
  width: 100%;
  height: 100%;
}

h1 {
  font-family: "Merriweather";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Change auto complete styles in Chrome */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus textarea:-webkit-autofill,
textarea:-webkit-autofill:hover textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 1px solid rgb(66, 119, 216);
  -webkit-text-fill-color: rgb(66, 119, 216);
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
  transition: background-color 5000s ease-in-out 0s;
}

.error-container {
  position: relative;
  height: auto;
  width: 300px;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 15px 15px;
  margin: 0 0 25px;
  border-radius: 4px;
}

.error-container__errors-list {
  padding: 0 35px;
}

.error-container__title {
  text-align: center;
}

.error-container__error-message {
  color: rgb(182, 26, 26);
}

.error-container__error-message ~ .error-container__error-message {
  margin: 10px 0;
}

.error-container__error-message:last-child {
  margin-bottom: 0;
}

.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  height: 100%;
  background-image: url("/images/star-gazing.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.login__outline {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
  min-height: 95%;
  border-radius: 4px;
  box-shadow: 1px 1px 35px rgb(143, 143, 143);
  background-color: rgba(255, 255, 255, 0.8);
}

.login__header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login__h3 {
  margin-bottom: 40px;
}

.login__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 15px;
  width: 100%;
}

.login__form fieldset {
  display: flex;
  flex-direction: column;
  width: 53%;
  border: none;
  padding: 0 0;
  margin: 0 0;
  box-sizing: border-box;
}

.login__form label {
  align-self: flex-start;
  margin-bottom: 5px;
  padding-left: 3px;
  font-size: 15px;
}

.login__form input {
  margin-bottom: 25px;
  width: 100%;
  border: none;
}

.login__form input[type="text"],
.login__form input[type="password"] {
  border-radius: 4px;
  height: 35px;
  font-size: 16px;
  padding-left: 7px;
  box-shadow: 1px 1px 10px lightgrey;
}

.login__form input[type="text"]::-webkit-input-placeholder,
.login__form input[type="password"]::-webkit-input-placeholder {
  color: rgb(145, 164, 184);
  font-weight: 100;
}

.login__form input[type="text"]::-ms-input-placeholder,
.login__form input[type="password"]::-ms-input-placeholder {
  color: rgb(145, 164, 184);
  font-weight: 100;
}

.login__form input[type="text"]::placeholder,
.login__form input[type="password"]::placeholder {
  color: rgb(145, 164, 184);
  font-weight: 100;
}

.login__btn {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  background-color: green;
  color: white;
  font-size: 15px;
  font-weight: 700;
  cursor: pointer;
  border: none;
  box-shadow: 3px 3px 5px gray;
  outline: none;
  transition: box-shadow 100ms ease, -webkit-transform 100ms ease;
  transition: transform 100ms ease, box-shadow 100ms ease;
  transition: transform 100ms ease, box-shadow 100ms ease, -webkit-transform 100ms ease;
  padding: 0 0;
}

.login__btn:active {
  -webkit-transform: translate(1.5px, 1.5px);
          transform: translate(1.5px, 1.5px);
  box-shadow: 1px 1px 3px gray;
}

.login__register-link {
  text-decoration: none;
  color: cadetblue;
  border-bottom: 1px dashed green;
  margin-bottom: 15px;
}

.login__register-link:hover {
  color: rgb(115, 185, 187);
}

.sign-up {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background: url("/images/more-star-gazing.jpg");
  background-size: cover;
}

.sign-up__outline {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
  padding: 30px 0;
  border-radius: 4px;
  box-shadow: 1px 1px 35px rgb(143, 143, 143);
  background-color: rgba(255, 255, 255, 0.8);
}

@media screen and (max-width: 650px) {
  .sign-up__outline {
    width: 100%;
    min-height: 100vh;
  }
}

.sign-up__header {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.sign-up__h1,
.sign-up__h2 {
  margin-top: 0;
}

.sign-up__form {
  display: flex;
  flex-direction: column;
  height: auto;
  position: relative;
}

.sign-up__form label {
  padding-left: 3px;
  color: rgb(36, 36, 36);
  font-size: 15px;
}

.sign-up__form input[type="text"],
.sign-up__form input[type="password"] {
  width: 300px;
  height: 32px;
  border: none;
  border-radius: 4px;
  box-shadow: 1px 1px 10px lightgrey;
  margin: 5px 0 15px;
  padding-left: 10px;
  font-size: 16px;
}

.sign-up__form input[type="submit"] {
  font-size: 16px;
  margin-top: 15px;
  background-color: rgb(63, 143, 146);
  color: white;
  border-radius: 4px;
  height: 40px;
  outline: none;
  border: none;
  box-shadow: 3px 3px 5px gray;
  transition: box-shadow 100ms ease, -webkit-transform 100ms ease;
  transition: transform 100ms ease, box-shadow 100ms ease;
  transition: transform 100ms ease, box-shadow 100ms ease, -webkit-transform 100ms ease;
  padding: 0 0;
}

.sign-up__form input[type="submit"]:active {
  -webkit-transform: translate(1.5px, 1.5px);
          transform: translate(1.5px, 1.5px);
  box-shadow: 1px 1px 3px gray;
}

/* Wrapper styles */

.chatterbox {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

/* Top nav styles */

.chatterbox header {
  display: flex;
  width: 100%;
  height: 70px;
  background-color: #2c2f36;
  color: white;
  box-shadow: 5px 5px 15px black;
}

.top-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0 0;
  margin: 0 0;
  height: 100%;
  width: 100%;
  padding: 0 15px;
}

.top-nav h1 {
  font-size: 28px;
}

.top-nav__menu {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  font-size: 14px;
  height: 48px;
  width: 48px;
  border-radius: 50%;
  background-color: white;
  color: #2c2f36;
  transition: 400ms ease border-radius;
}

.top-nav__menu:hover {
  border-radius: 20px 0 20px 0;
}

/* Main interface wrapper styles */

.chatterbox main {
  display: flex;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

/* Inventory styles */

.chatterbox__inventory {
  display: flex;
  flex-direction: column;
  background-color: lightblue;
  width: 275px;
  box-sizing: border-box;
  border-right: 1px solid black;
  background-color: #464b57;
}

.chatterbox__online-users {
  background-color: #353841;
  padding: 0 0;
  box-sizing: border-box;
  height: 100%;
  transition: 100ms ease background-color;
  color: white;
  display: flex;
  flex-direction: column;
  list-style: none;
  margin: 0 0;
}

.chatterbox__online-users li:first-child {
  border-bottom: 1px solid #2c2f36;
  box-shadow: 0px 5px 5px #2c2f36;
}

.chatterbox__online-users li {
  height: 50px;
  display: flex;
  align-items: center;
  font-size: 16px;
  padding-left: 15px;
}

.chatterbox__online-users li ~ li::before {
  background-color: limegreen;
  color: #5d6275;
  height: 8px;
  width: 8px;
  content: "";
  position: relative;
  border-radius: 50%;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 26px;
  font-weight: 900;
}

.chatterbox__online-users li#no-users-online {
  position: relative;
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
  padding: 0 15px;
  height: auto;
  width: 100%;
  text-align: center;
}

.chatterbox__online-users li#no-users-online::before {
  width: 0px;
  height: 0px;
}

/* Conversation styles */

.chatterbox__messaging-container {
  display: flex;
  flex-direction: column;
  flex: 10 1;
  min-width: 275px;
  background-color: #464b57;
}

.chatterbox__conversation-bin {
  width: 100%;
  flex: 20 1;
  list-style: none;
  display: flex;
  flex-direction: column;
  padding: 15px 20px;
  margin: 0 0;
  overflow-y: auto;
  overflow-x: hidden;
}

.chatterbox__conversation-bin::-webkit-scrollbar {
  width: 8px;
}

.chatterbox__conversation-bin::-webkit-scrollbar-thumb {
  background-color: #5d6275;
  outline: 1px solid slategrey;
  border-radius: 15px;
}

li.chatterbox__message {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex: 0 0 auto;
  margin-bottom: 18px;
}

.chatterbox__user-info {
  display: flex;
  align-items: baseline;
}

.chatterbox__message-received .chatterbox__user-info {
  align-self: flex-start;
}

.chatterbox__message-sent .chatterbox__user-info {
  margin-right: 8px;
}

.chatterbox__user-info p:first-child {
  color: white;
  font-size: 15px;
  margin: 0 3px 6px;
}

.chatterbox__user-info p {
  padding-left: 5px;
  margin: 0 0 6px;
  color: #888c97;
  font-size: 13px;
}

.chatterbox__message span {
  padding: 10px 10px;
  font-size: 15px;
  color: white;
  background-color: #353841;
  max-width: 45%;
  word-wrap: break-word;
}

@media only screen and (max-width: 950px) {
  .chatterbox__message span {
    max-width: 60%;
  }
}

@media only screen and (max-width: 770px) {
  .chatterbox__message span {
    max-width: 70%;
  }
}

.chatterbox__message-sent span {
  border-radius: 0 10px 0 10px;
}

.chatterbox__message-received span {
  border-radius: 10px 0 10px 0;
  align-self: flex-start;
}

.chatterbox__user-is-typing {
  color: white;
  font-size: 14px;
  font-style: italic;
}

.chatterbox__group-update {
  color: white;
  font-size: 13px;
  font-style: italic;
  display: flex;
  justify-content: center;
  margin-bottom: 18px;
}
/* Form Styles */

.chatterbox__form {
  display: flex;
  width: 100%;
  height: 100px;
  box-shadow: 3px 0px 20px rgb(17, 17, 17);
  background-color: #353841;
}

.chatterbox__type-message {
  box-sizing: border-box;
  background-color: #464b57;
  border: 1px solid #3c4049;
  flex: 1 1;
  border-radius: 6px;
  padding: 8px 10px;
  margin: 15px;
  font-size: 14px;
  resize: none;
  outline: none;
  overflow: auto;
  color: white;
}

.chatterbox__type-message:focus {
  outline: none;
}

.chatterbox__type-message::-webkit-input-placeholder {
  color: white;
}

.chatterbox__type-message::-ms-input-placeholder {
  color: white;
}

.chatterbox__type-message::placeholder {
  color: white;
}

